.form {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.formInput {
    background: #1e1e1e;
    border: 1px solid rgba(190, 219, 176, 0.5);
    border-radius: 8px;
    padding: 10px;
    margin-bottom: 24px;
    color: #ffffff;
    width: 100%;
    height: 49px;
    box-sizing: border-box;

    font-size: 14px;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: -0.02em;
}