.button {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	height: 49px;
	border: 0;
	border-radius: 8px;
	padding: 0 12px;
	background: #bedbb0;
	color: #161616;
}

@media (hover: hover) and (pointer: fine) {

	.button:hover,
	.button:focus,
	.button:active {
		background: #a1cf89;
	}

	.button:active {
		transform: scale(0.95);
	}
}

.buttonText {
	font-size: 14px;
	font-weight: 500;
	line-height: 21px;
	letter-spacing: -0.02em;
}

.iconWrap {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 28px;
	height: 28px;
	border-radius: 5px;
	margin-right: 8px;
	background: #161616;
	flex: 0 0 28px;
}

.icon {
	width: 14px;
	height: 14px;
	stroke: #fff;
	background: transparent;
}