.input {
    width: 100%;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    letter-spacing: -0.02em;
    color: #fff;
    border: 1px solid #bedbb0;
    border-radius: 8px;
    box-shadow: 0 4px 16px 0 rgba(22, 22, 22, 0.08);
    background: #1f1f1f;
    opacity: 0.4;
    padding: 14px 18px;
}

.input:focus {
    outline: none;
    opacity: 1;
}

.textarea {
    resize: none;
    height: 120px;
    margin: 14px 0 24px;
}